export default {
  data () {
    return {
    }
  },
  components: {
  },
  methods: {
    getSetting(){
      let _this = this;
      wx.login({
        success: async function (reslogin) {
          if(reslogin.code){
            let url = "api/wx/session";
            let data = {
              code:reslogin.code
            }
            let res1 = await _this.$post(url,data);
            wx.setStorageSync('userSession', res1)
            console.log("后台请求数据："+res1);
          }
        }
      })
    },
    //查询记录页面
    toList(){
      const url = '../list/main'
      wx.navigateTo({ url })
    },
    //个人中心
    toUserInfo(){
      const url = '../user/main'
      wx.navigateTo({ url })
    },
  },
  mounted(){
    if(wx.getStorageSync('userSession').token == null || wx.getStorageSync('userSession').token == undefined){
      this.getSetting();
    }
  },
  created () {
  }
}
